.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    background: white;
}

.footer-logo {
    display: flex;
    align-items: center;
    gap: 10px;
}

.footer-logo p {
    color: #383838;
    font-size: 46px;
    font-weight: 700;  
}

.footer-links {
    display: flex;
    list-style: none;
    gap: 50px;
    color: #252525;
    font-size: 20px;
}

.footer-links li {
    cursor: pointer;
}

.footer-social-icon {
    gap: 20px;
}

.footer-copyright {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 30px;
    color: #1a1a1a;
    font-size: 20px;
}

.footer-copyright hr {
    width: 300%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background-color: #c7c7c7;  
}

.icon{
    display: flex;
    justify-content: space-between;
}

@media(max-width:800px){
    .footer{
        gap: 20px;
    }

    .footer-logo{
     gap: 10px;
     align-items: end;
    }

    .footer-logo img{
        width: 40px;
    }

    .footer-logo p{
        font-size: 25px;
    }

    .footer-links{
        font-size: 14px;
        gap: 10px;
    }

    .footer-social-icon img{
        width: 22px;
    }
    
    .footer-copyright{
        font-size: 13px;
    }
}

